<template>
<transition>
        <div class="header">
                <img :class='labelNow' :src="getImgUrl('label1.png')" alt="">
                <img class='header_img1' :src="getImgUrl('title1a.png')" alt="">
                <img class='header_img2' :src="getImgUrl('title1b.png')" alt="">
                <div class="phone_number"><a title="Позвонить" href='tel:+79528368680'>тел. +7(952)-836-86-80</a></div>
        </div>
</transition>
</template>

<script>
export default {
    methods: {
        getImgUrl(pic) {
            return require('@/assets/Images/' + pic)
            }
    },
    data() {
        return {
            labelNow: "label1"
        }
    },
    mounted()  {
        setInterval(() => {
                this.labelNow = (this.labelNow === "label1") ? "label2" : "label1"
        }, 1000*60*5);
    }

}
</script>

<style scoped>
.label1 {
        margin: 0 1vmax 0 0vmax;
        position: relative;
        height: 74%;
        top: -0.1vmax;
        animation:  anim1 2s;
}

.label2 {
        margin: 0 1vmax 0 0vmax;
        position: relative;
        height: 74%;
        top: -0.1vmax;
        animation:  anim2 2s;
}

.header {
        height: 17vh;
}
.header_img1 {
        position: absolute;
        left: 13vmax;
        top: 0;
        height: 27%;
        animation: animTitle1 1s;
}
.header_img2 {
        position: absolute;
        left: 13vmax;
        top: 31%;
        height: 36%;
        animation: animTitle2 1s;
}
.phone_number {
        margin-top: 1vmax;
        font-size: 2.5vmax;
}

a{
        color: #000;
        text-decoration: none;
}

@media screen and (orientation: landscape){
    .header {
        height: 17vw;
    }
    .label1 {
        margin: 0 1vmax 0 0vmax;
    }
    .label2 {
        margin: 0 1vmax 0 0vmax;
    }
}
@keyframes animTitle1 {
        0% {
                transform: translateY(-100%);
        }
        100% {
                transform: translateY(0);
        }
}

@keyframes animTitle2 {
        0% {
                transform: scaleY(0);
        }
        100% {
                transform: scaleY(1);
        }
}

@keyframes anim1 {
        0% {
                transform: scaleY(1);
        }
        50% {
                transform: scaleY(-1)
        }
        100% {
                transform: scaleY(1)
        }
}
@keyframes anim2 {
        0% {
                transform: scaleY(1);
        }
        50% {
                transform: scaleY(-1)
        }
        100% {
                transform: scaleY(1)
        }
}

</style>