<template>
    <transition name = 'modal'>
       <div class = 'modal-mask' :style= "opacityModal">
           <div class = 'modal-wrapper' >
               <div :class="modalContainer" @click= "closeModal">
                    <ModalHeader>
                        <h1 slot="header">{{modalHeaderText}}</h1>
                    </ModalHeader>
                    <ModalBody>
                        <h2 slot="body">{{modalBodyText}}</h2>
                    </ModalBody>
                    <ModalFooter>
                       <h3 slot="footer">{{modalFooterText}}</h3>
                    </ModalFooter>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import ModalHeader from '@/components/ModalHeader'
import ModalBody from '@/components/ModalBody'
import ModalFooter from '@/components/ModalFooter'
export default {
    components: {
        ModalHeader,
        ModalBody,
        ModalFooter
    },
    props: {
        modalHeaderText: String,
        modalBodyText: String,
        modalFooterText: String
    },
    methods: {
        closeModal() {
            this.modalContainer = "modal-container-leave"
             this.opacityModal =  "opacity: 0"
            setTimeout(() => {
                this.$emit('closeModal')
            }, 500);
        }
    },

    data() {
        return {
            modalContainer: "modal-container",
            opacityModal: "opacity:0"
        }
    },
    mounted() {
        setTimeout(() => {
            this.opacityModal =  "opacity: 0.8"
        }, 50);
    }
}
</script>

<style scoped>
.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0;
    display: table;
    transition: opacity 700ms ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 50%;
  margin: 0 auto;
  background-color: #FFFFFF;
  border-radius: 1em;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-container-leave {
  width: 50%;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #FFFFFF;
  border-radius: 1em;
  font-family: Helvetica, Arial, sans-serif;
  transform: scale(5);
  transition: transform 0.3s ease;
}

</style>