<template>
        <div>
            <table>
                <tr >
                    <td colspan="3" class="question1">ВЫБЕРИТЕ ВИД УБОРКИ:</td>
                </tr>
                <tr>
                    <td :class='titleFocusedMethod(1)' @click="focusedNum = 1; $emit('moveFocus', 1)">ЕЖЕНЕДЕЛЬНАЯ</td>
                    <td :class='titleFocusedMethod(2)' @click="focusedNum = 2; $emit('moveFocus', 2)">ГЕНЕРАЛЬНАЯ</td>
                    <td :class='titleFocusedMethod(3)' @click="focusedNum = 3; $emit('moveFocus', 3)">ПОСЛЕ РЕМОНТА</td>
                </tr>
                <tr>
                    <td :class='focusedMethod(1)'>
                            <img  class="img_repair" :src='getImgUrl("usual1.jpg")' @click="focusedNum = 1; $emit('moveFocus', 1)" />
                    </td>
                    <td :class='focusedMethod(2)'>
                            <img class="img_repair" :src='getImgUrl("general1.jpg")' @click="focusedNum = 2; $emit('moveFocus', 2)"/>
                    </td>
                    <td :class='focusedMethod(3)'>
                            <img class="img_repair" :src='getImgUrl("remont2.jpg")' @click="focusedNum = 3; $emit('moveFocus', 3)"/>
                    </td>
                </tr>
            </table>
                <calcCleanWhatToDoList :whatToDo="focusedNum" />
        </div>
</template>

<script>
import calcCleanWhatToDoList from '@/components/CalcCleanWhatToDoList'
import {mapGetters} from 'vuex'

export default {
        components: {
                calcCleanWhatToDoList
        },
        methods: {
                getImgUrl(pic) {
                return require('@/assets/Images/' + pic)
                },
                focusedMethod(num) {
                        return (this.focusedNum === num) ? "focused" : "unfocused"
                },
                titleFocusedMethod(num) {
                        return (this.focusedNum === num) ? "title_focused" : "title_unfocused"
                }
        },
        computed: mapGetters(['getFocusedNum']),

        data() {
            return{
                focusedNum: 1
            }
        },
        mounted() {
                this.focusedNum = this.getFocusedNum
                this.$emit('moveFocus', this.focusedNum)
        },
        beforeDestroy() {
                this.$store.dispatch('setFocusedNum', this.focusedNum)
        }
}
</script>

<style scope>
.question1 {
        padding-bottom: 0.5vmax;
        height: 2em;
        vertical-align: middle;
        color: rgba(191, 38, 51, 1);
        font-family: 'Fira Sans Extra Condensed', Arial, Helvetica, sans-serif;
        font-weight: 400;
        font-size: 3vmax;
        text-align: center;
}

table {
        width: 100%;
        font-family: 'Fira Sans Extra Condensed', Arial, Helvetica, sans-serif;
        font-weight: 400;
}

td {
        width: 33%;
        height: 4vmax;
        text-align: center;
        font-size: 2vmax;
        cursor: default;
}

.img_repair {
        width: 100%;
        border-radius: 0.5em;
        vertical-align: middle;
 }
.title_focused {
        background-color: rgba(150, 172, 217, 1);
        border-radius: 0.5em;
        transition: all 0.5s;
        color: rgba(255, 255, 255, 1);
        font-weight: 700;
 }
 .title_unfocused{
        background-color: none;
        transition: all 0.5s;
        color: rgba(0, 0, 0, 1);
        font-weight: bold;
 }

 .focused {
        background-color: rgba(191, 38, 51, 1);
        border-radius: 0.5em;
        transition: all 0.5s;
 }

 .unfocused{
        background-color: none;
        transition: all 0.5s;
        transform: scale(0.85);
 }

</style>