<template>
        <div>
                <div class="question3">ДОПОЛНИТЕЛЬНЫЕ УСЛУГИ:</div>
                <div v-show="sumTotal !==0 && !isOrder" class="fee">стоимость проезда без основной уборки (+100 руб.):</div>
                <div class="options_box">
                        <div v-for="(optionsJob, num) in optionsJobs" class="option_list" :key="num">
                                <label>
                                        <input type="checkbox" :name="'checkbox' + num" :id="'checkbox' + num"  :value ="num" v-model="selected" @change="changeAct"/>
                                        <span :style="getStyle(num)"></span>
                                        <span class="option_job">{{optionsJob}} - от {{prices[num]}} руб.</span> 
                                 </label>
                                <div v-if="selected.indexOf(num) != -1" class="num_label">кол-во:
                                        <input class="input_num" type="text" :name="'text'+num" :id="'text'+num" v-model.number.lazy="numSelected[num]" @change="changeAct" />
                                </div>
                        </div>            
                </div>

        </div>
</template>

<script>

import {mapGetters} from 'vuex'
export default {
        props: {
                isOrder: Boolean
        },
        methods: {
                changeAct() {
                        this.sumTotal = 0;
                        for (let i = 0; i < this.selected.length; i++) {
                                if (!Number(this.numSelected[this.selected[i]])) { this.numSelected[this.selected[i]] = 1 }
                                this.sumTotal += this.prices[this.selected[i]] * this.numSelected[this.selected[i]]
                        }
                        this.$emit('optionsTotal', this.sumTotal)
                },
                getStyle(num) {
                        return {width: '3vmax',
                                height: '3vmax',
                                backgroundImage: (this.selected.indexOf(num) != -1) ? 'url(' + require('@/assets/Images/001-done.png') + ')' :  'url(' + require('@/assets/Images/002-frame.png') + ')' ,
                                backgroundRepeat: 'no-repeat',
                                backgroundSize : 'cover'
                        }
                }
        },
        computed: mapGetters(["getNumSelected", "getSelected"]),
        data() {
                return {
                        sumTotal: 0,
                        numSelected: [],
                        selected: [],
                        prices: [
                                200,
                                700,
                                1000,
                                300,
                                400,
                                300,
                                400,
                                300,
                                500,
                                350,
                                300,
                                300
                        ],
                        optionsJobs: [
                                "мойка обычного окна (шт.)",
                                "м. балконного окна (шт.)",
                                "м. балкона, лоджии (шт.)",
                                "м. жалюзи (шт.)",
                                "м. люстр, бра и т.п. (шт.)",
                                "м. внутри шкафов (шт.)",
                                "м. духового шкафа (шт.)",
                                "м. СВЧ печи (шт.)",
                                "м. холодильника (шт.)",
                                "м. кухонных шкафов (шт.)",
                                "м. посуды (за 1 час)",
                                "глажка белья (за 1 час)"
                        ]
                }
        },
        mounted() {
                this.numSelected = this.getNumSelected
                this.selected = this.getSelected
                this.changeAct()
        },
        beforeDestroy() {
                this.$store.dispatch('setNumSelected', this.numSelected)
                this.$store.dispatch('setSelected', this.selected)
        }
}
</script>

<style scope>
.question3 {
        margin-top: 4vmax;
        color: rgba(191, 38, 51, 1);
        font-family: 'Fira Sans Extra Condensed', Arial, Helvetica, sans-serif;
        font-weight: 400;
        font-size: 3vmax;
        text-align: center;
}

.fee {
        margin-top: 0.5vmax;
        vertical-align: middle;
        color: rgba(191, 38, 51, 1);
        font-family: 'Fira Sans Extra Condensed', Arial, Helvetica, sans-serif;
        font-weight: 400;
        font-size: 2vmax;
        text-align: center;
}

.options_box {
        margin-top: 2vmax;
        width: 98%;
        border-radius: 1rem;
        border-style: dotted;
        border-width: 2px;
        border-color: rgba(191, 38, 51, 1);
        padding: 1vmax 0;
}
.option_list {
        position: relative;
        margin-left: 1vw;
        padding-bottom: 1vmax;
        font-family: 'Yanone Kaffeesatz', sans-serif;
        font-weight: 400;
        font-size: 3.5vmax;
        color:rgba(0, 0, 0, 1);
}

.num_label {
        margin-left: 10%;
        color:rgba(191, 38, 51, 1);
}

.input_num {
        margin-left: 1%;
        width: 5vw;
        font-family: 'Yanone Kaffeesatz', sans-serif;
        font-weight: 700;
        font-size: 3vmax;
        color:rgba(0, 0, 0, 1);
        text-align: center;
}
label {
        width: 3vmax;
        height: 3vmax;
        cursor: pointer;
        margin-right: 0;
}
input[type=checkbox]{
        vertical-align: middle;
        opacity: 0;
}
input[type=checkbox] + span{
        position: absolute;
        cursor: pointer;
        left: 0;
        top: 0;
        animation: chk1 0.5s;
        opacity: 0.7;
}

input[type=checkbox]:checked + span{
        animation: chk2 0.5s;
        opacity: 1;
}

@keyframes chk1{
        0% { 
                opacity: 1;
                transform: scale(1.1)
        }
        40% {
                opacity: 1;
                transform: scale(0.9)
        }
        65% {
                opacity: 1;
                transform: scale(1.1)
        }
        82% {
                opacity: 1;
                transform: scale(0.9)
        }
        93% {
                opacity: 1;
                transform:scale(1.1)
        }
        100% {
                transform: scale(1);
                opacity: 0.7;
        }
}

@keyframes chk2{
        0% {
                transform: scale(1.1)
        }
        40% {
                transform: scale(0.9)
        }
        65% {
                transform: scale(1.1)
        }
        82% {
                transform: scale(0.9)
        }
        93% {
                transform:scale(1.1)
        }
        100% {
                transform: scale(1)
        }
}

.option_job {
        margin-left: 0.5vmax;       
}

@media screen and (orientation: landscape){
        .option_job {
                margin-left: 3vmax;
        }
}
</style>