<template>
    <div>
          <router-link class="menu" to="/">ГЛАВНАЯ</router-link>
          <router-link class="menu" to="/about">О НАС</router-link>
    </div>
</template>

<script>
export default {
        methods: {
                getCookie(name) {
                        let matches = document.cookie.match(new RegExp(
                                "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
                        ))
                        return matches ? decodeURIComponent(matches[1]) : undefined
                },
        },
        mounted() {
                const path = this.getCookie('PATH')
                if (path !=='undefined') {
                        document.cookie = 'PATH=/; domain=.test2.toort.ru; max-age=-1'
                        if (this.$router.options.routes.findIndex((value => {
                                if (value.path === path) return true
                        })) !== -1) {
                                this.$router.replace(path)
                        }
                }
        }
}
</script>

<style scope>
.menu {
        position: relative;
        padding: 0 1vmax;
        border-radius: 0.5vmax;
        text-decoration: none;
        font-family: 'Fira Sans Extra Condensed', 'Exo 2', 'Arial', 'Helvetica', 'sans-sherif';
        font-size: 2.5vmax;
        color: rgba(150, 172, 217, 1);
        transition: all 0.5s;
}
.menu:hover {
        color: rgb(255, 255, 255, 1);
        background-color: rgba(150, 172, 217, 1);
        border-radius: 0.5vmax;
        transition: all 0.5s;
}
.router-link-exact-active {
        position: relative;
        margin: 0 1vmax ;
        padding: 0.5vmax 1vmax;
        background-color: rgba(191, 38, 51, 1);
        border-radius: 0.5vmax;
        text-decoration: none;
        font-family: 'Fira Sans Extra Condensed', 'Exo 2', 'Arial', 'Helvetica', 'sans-sherif';
        font-size: 2.5vmax;
        color: rgb(255, 255, 255);
        transition: all 0.5s;
}
.router-link-exact-active:hover {
        background-color: rgba(191, 38, 51, 1);
}
</style>