<template>
    <div>
            <div class="question2">УКАЖИТЕ РАЗМЕР ПОМЕЩЕНИЯ:</div>
            <div class="select_size">
                    <select  name="sizeHouse" id="sizeHouse" v-model="sizeHouseSelect" @change="$emit('sizeHouseSelectAct', sizeHouseSelect)">
                            <option value="0">не указано</option>
                            <option value="1200">менее 39 м2</option>
                            <option value="1300">от 40 до 59 м2</option>
                            <option value="1400">от 60 до 99 м2</option>
                            <option value="1600">от 100 до 149 м2</option>
                            <option value="1800">от 150 до 199 м2</option>
                            <option value="2000">от 200 до 249 м2</option>
                            <option value="2200">более 250 м2</option>
                     </select>
                    <div class="correction_price" v-show="sizeHouseSelect >0">Цена корректируется после осмотра помещения</div>
            </div>
    </div>
</template>

<script>

import {mapGetters} from 'vuex'

export default {
    computed: {
            ...mapGetters(['getSizeHouseSelect']),
            },
    data() {
        return {
            sizeHouseSelect: 0
        }
    },
    mounted() {
            this.sizeHouseSelect = this.getSizeHouseSelect
            this.$emit('sizeHouseSelectAct', this.sizeHouseSelect)
    },
    beforeDestroy() {
            this.$store.commit('updateSizeHouseSelect', this.sizeHouseSelect)
    }
}
</script>

