import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store ({
        state: {
                totalCalc: 0,
                numSelected: [],
                selected: [],
                sizeHouseSelect: 0,
                focusedNum: 1
        },
        getters: {
                getTotalCalc (state) { return state.totalCalc },

                getNumSelected (state){ return state.numSelected },

                getSelected (state){ return state.selected },

                getSizeHouseSelect (state) { return state.sizeHouseSelect },

                getFocusedNum (state) { return state.focusedNum }
        },
        mutations: {
                updateTotalCalc (state, totalCalc) {
                        state.totalCalc = totalCalc
                },
                updateNumSelected (state, numSelected) {
                        state.numSelected = numSelected
                },
                updateSelected (state, selected) {
                        state.selected = selected
                },

                updateSizeHouseSelect(state, sizeHouseSelect) {state.sizeHouseSelect = sizeHouseSelect},

                updateFocusedNum(state, focusedNum) {state.focusedNum = focusedNum}
        },
        actions: {
            setTotalCalc (ctx, totalCalc = 0) {
                    ctx.commit('updateTotalCalc', totalCalc)
            },
            setNumSelected (ctx, numSelected) {
                    ctx.commit('updateNumSelected', numSelected)
            },
            setSelected (ctx, selected) { ctx.commit('updateSelected', selected) },

            setFocusedNum (ctx, focusedNum) { ctx.commit('updateFocusedNum', focusedNum) },
        },
})

export default store