<template>
<transition>
        <div>
            <CalcCleanTotal/>
            <CalcCleanSizeHouse @sizeHouseSelectAct="sizeHouseSelectAct"/>
            <CalcCleanWhatToDo  @moveFocus="moveFocusAct" />
            <CalcCleanOptions @optionsTotal="optionsTotalAct" :isOrder='sizeHouse > 0'/>
        </div>
</transition>
</template>

<script>
import CalcCleanTotal from '@/components/CalcCleanTotal'
import CalcCleanWhatToDo from '@/components/CalcCleanWhatToDo'
import CalcCleanSizeHouse from '@/components/CalcCleanSizeHouse'
import CalcCleanOptions from '@/components/CalcCleanOptions'

export default {
        components: {
            CalcCleanTotal,
            CalcCleanWhatToDo,
            CalcCleanSizeHouse,
            CalcCleanOptions
        },
        methods: {
                setTotalCalc() {
                        this.$store.dispatch('setTotalCalc', this.totalCalcComp)
                },
                moveFocusAct(event) {
                        this.whatToDo =  event
                        this.setTotalCalc(this.totalCalcComp)
                },
                sizeHouseSelectAct(event) {
                        this.sizeHouse = event
                        this.setTotalCalc(this.totalCalcComp)
                },
                optionsTotalAct(event) {
                        this.optionsTotal = event
                        this.setTotalCalc(this.totalCalcComp)
                }
       },
        computed: {
                  totalCalcComp() {
                        let totalSum = this.sizeHouse  *  this.whatToDoCoef[this.whatToDo -1] + this.optionsTotal
                            return totalSum ===0 ? 0 : totalSum +  this.ticket
                  } 
        },
        data(){
            return{
                minLimit: 1000,
                baseTariff: 10,
                whatToDo: 1,
                whatToDoCoef: [1, 2.5, 4],
                sizeHouse: 0,
                ticket: 100,
                toolsTariff: 3.5,
                optionsTotal: 0
            }
        }
}
</script>

